<template>
	<view>
		<view>
			 
		</view>
		<view class="uni_detail">
			<view class="uni-divider-line mb20">
				<view class="uni-flex-list">
					<text class="detail_name">{{ detailDate.submitterName }}</text>
					<view @click="handleFollow">
						<image v-if="detailDate.isFollow== '1'" src="../../static/images/02.png" mode="" class="list_follow uni-vertical-align"></image>
						<image v-if="detailDate.isFollow == '2'" src="../../static/images/03.png" mode="" class="list_follow uni-vertical-align"></image>
						<text class="pl8 f14 name_color">{{ detailDate.isFollowDesc }}</text>
					</view>
				</view>
				<view class="mb15">
					<text class="name_color">诉求编号：</text>
					<text class="name_color">{{ detailDate.networkId }}</text>
				</view>
			</view>
			<view>
				<text class="uni_c33 fontw6 f16">{{detailDate.networkTitle}}</text>
				<view class="f16 uni_c33 mb15 mt15">
					{{detailDate.networkContent}}
				</view>
				<view class="uni-flex-list_img">
					<view class="img" v-for="itemson in detailDate.networkFile">
						<image v-if="!imageError" :src="itemson" alt="" class="uni_width uni_height"  @error="handle_image_error"></image>
						<view v-else class="imageError img uni_width uni_height" >
							加载失败
						</view>
					</view>
				</view>
				<text class="f12 uni_c93">{{detailDate.submitTime}}</text>
			</view>
			<view class="detail_notice" v-if="detailDate.isOffice == 1">
				<view class="uni-flex-list mb15">
					<text class="replayTip f14 fontw6">平城热线回复：</text>
					<text class="f12 uni_c93">{{detailDate.workNetworkOfficeResp.officeTime}}</text>
				</view>
				<text class="f14 detail_reply">{{detailDate.workNetworkOfficeResp.networkOfficeContent}}</text>
				<view class="mt20 f12 detail_company">
					<text>处办单位：</text>
					<text>{{detailDate.workNetworkOfficeResp.officeUnit}}</text>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	components: {},
	data() {
		return {
			imgPath: this.$config.imgPath,
			detailDate: {},
			networkId:'',
			imageError:false,
		};
	},

	async onLoad(options) {
		let id = options.id;
		this.networkId = id;
		if (id) {
			this.loadData(id);
		}
	},

	methods: {
		//带下拉刷新和上滑加载
		loadData(id) {
			this.$minApi.networkWorkOrderGet({networkId:id}).then(res => {
				res.data.networkFile = JSON.parse(res.data.networkFile);
				this.detailDate = res.data;
				console.log(this.detailDate)
			});
		},
		handle_image_error: function(index) {
			this.imageError = true;
		},
		/*S 取消关注 */
		handleFollow() {
			console.log(this.networkId);
			var followlist = {
				networkId: this.networkId,
				userId: '123',
				deviceId: '',
				sessionId: ''
			};
			if (this.detailDate.isFollow == '2') {
				// 未关注
				this.$minApi.networkWorkOrderCancelFollow({ ...followlist }).then(res => {
					if(res.code == 0){
						uni.showToast({
						    title: res.msg,
						    duration: 2000
						});
						this.loadData(this.networkId)
					}else{
						uni.showToast({
						    title: res.msg,
						    duration: 2000,
							icon:'none'
						});
						this.loadData(this.networkId)
					}
					
				});
			} else {
				// 已关注
				this.$minApi.networkWorkOrderFollow({ ...followlist }).then(res => {
					if(res.code == 0){
						uni.showToast({
						    title: res.msg,
						    duration: 2000
						});
						this.loadData(this.networkId)
					}else{
						uni.showToast({
						    title: res.msg,
						    duration: 2000,
							icon:'none'
						});
						this.loadData(this.networkId)
					}
				});
			}
		},
		/*E 取消关注*/
	}
};
</script>

<style lang="scss">
.uni_detail {
	padding: 30upx;
	.list_follow {
		width: 28upx;
		height: 28upx;
	}
	.detail_name {
		font-size: 32upx;
		color: #333333;
	}
	.detail_notice{
		background-color: #F5F9FF; 
		padding:30upx;
		margin-top: 40upx;
	}
	.replayTip{
		color: #3893FE;
	}
	.detail_reply{
		color: #414141;
	}
	.detail_company{
		color: #404040;
	}
	.uni-flex-list_img {
		display: flex;
		justify-content: flex-start;
		flex-wrap: wrap;
		align-items: center;
	}
	.img {
		// border:2upx solid #000000;
		margin-bottom: 10upx;
		margin-right: 5upx;
	}
	.img:nth-child(3n) {
		margin-right: -2upx;
	}
	.imageError {
		background: #f5f7fa;
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 14px;
		color: #c0c4cc;
		vertical-align: middle;
	}
}
</style>
